'use client';
import { cva } from 'class-variance-authority';
import { twMerge } from 'tailwind-merge';
import { FunctionalIcon } from '../icon/Icon';
import { Spacing } from '../spacing';
import { ButtonProps, ButtonVariant } from './button.interface';

export const buttonVariants = {
  [ButtonVariant.Primary]: [
    'zep-bg-primary-default',
    'zep-text-primary-contrast',
    'enabled:hover:zep-bg-primary-hover',
    'enabled:hover:zep-text-primary-contrast',
    'enabled:active:zep-bg-primary-active',
    'enabled:active:zep-text-primary-contrast',
    'disabled:zep-opacity-disabled',
  ],
  [ButtonVariant.Secondary]: [
    'zep-bg-transparent',
    'zep-text-typography-primary-default',
    'enabled:hover:zep-bg-primary-hover',
    'enabled:hover:zep-text-primary-contrast',
    'enabled:active:zep-bg-primary-active',
    'enabled:active:zep-text-primary-contrast',
    'disabled:zep-opacity-disabled',
    'zep-ring-2',
    'zep-ring-inset',
    'zep-ring-primary-default',
    'enabled:hover:zep-ring-0',
    'enabled:active:zep-ring-0',
    'disabled:zep-ring-opacity-disabled',
  ],
  [ButtonVariant.Tertiary]: [
    'zep-bg-transparent',
    'zep-text-typography-primary-default',
    'enabled:hover:zep-bg-tertiary-hover',
    'enabled:hover:zep-text-typography-primary-hover',
    'enabled:active:zep-bg-tertiary-active',
    'enabled:active:zep-text-typography-primary-active',
    'disabled:zep-opacity-disabled',
  ],
  [ButtonVariant.DarkPrimary]: [
    'zep-bg-neutral-dark-default',
    'zep-text-neutral-dark-contrast',
    'enabled:hover:zep-bg-neutral-dark-hover',
    'enabled:active:zep-bg-neutral-dark-active',
    'disabled:zep-bg-opacity-disabled',
  ],
  [ButtonVariant.DarkSecondary]: [
    'zep-bg-transparent',
    'zep-text-typography-dark-100',
    'enabled:hover:zep-bg-neutral-dark-hover',
    'enabled:hover:zep-text-neutral-dark-contrast',
    'enabled:active:zep-bg-neutral-dark-active',
    'enabled:active:zep-text-neutral-dark-contrast',
    'disabled:zep-opacity-disabled',
    'zep-ring-2',
    'zep-ring-inset',
    'zep-ring-neutral-dark-default',
    'enabled:hover:zep-ring-0',
    'enabled:active:zep-ring-0',
  ],
  [ButtonVariant.DarkTertiary]: [
    'zep-bg-transparent',
    'zep-text-typography-dark-100',
    'enabled:hover:zep-bg-tertiary-hover',
    'enabled:active:zep-bg-tertiary-active',
    'disabled:zep-opacity-disabled',
  ],
  [ButtonVariant.LightPrimary]: [
    'zep-bg-neutral-light-default',
    'zep-text-neutral-light-contrast',
    'enabled:hover:zep-bg-neutral-light-hover',
    'enabled:active:zep-bg-neutral-light-active',
    'disabled:zep-bg-opacity-disabled',
  ],
  [ButtonVariant.LightSecondary]: [
    'zep-bg-transparent',
    'zep-text-typography-light-100',
    'enabled:hover:zep-bg-neutral-light-hover',
    'enabled:hover:zep-text-neutral-light-contrast',
    'enabled:active:zep-bg-neutral-light-active',
    'enabled:active:zep-text-neutral-light-contrast',
    'disabled:zep-opacity-disabled',
    'zep-ring-2',
    'zep-ring-inset',
    'zep-ring-neutral-light-default',
    'enabled:hover:zep-ring-0',
    'enabled:active:zep-ring-0',
  ],
  [ButtonVariant.LightTertiary]: [
    'zep-bg-transparent',
    'zep-text-typography-light-100',
    'enabled:hover:zep-bg-tertiary-hover',
    'enabled:active:zep-bg-tertiary-active',
    'enabled:hover:zep-text-neutral-light-contrast',
    'enabled:active:zep-text-neutral-light-contrast',
    'disabled:zep-opacity-disabled',
  ],
};
export const button = cva(
    [
      'zep-typography-button',
      'zep-rounded-button',
      'zep-px-1',
      'zep-py-0.75',
      'zep-flex',
      'zep-gap-0.5',
      'zep-rounded-4',
      'zep-justify-center',
      'focus-visible:zep-outline',
      'focus-visible:zep-outline-3',
      'focus-visible:zep-outline-offset-1',
      'focus-visible:zep-outline-focus',
    ],
    {
      variants: {
        variant: buttonVariants,
      },
      defaultVariants: {
        variant: ButtonVariant.Primary,
      },
    },
  );

export const Button: React.FC<ButtonProps> = ({
  className,
  variant,
  isLoading = false,
  label,
  children,
  icon,
  iconPosition = 'left',
  ...props
}: ButtonProps) => { 
  const isIconButton = !label || label === ""
  const buttonClass = isIconButton ? button({ variant }).replace('zep-px-1','zep-px-0.75') : button({ variant })
  return (
    <button className={twMerge(buttonClass, className)} {...props}>
      {isLoading && (
        <i className="mr-zep-0_5" data-testid="loading-icon">
          loading icon
        </i>
      )}
      <Spacing gap="0.5" className={iconPosition === 'left' ? 'zep-flex-row-reverse' : ''}>
        {label ?? children}
        {icon && <FunctionalIcon name={icon} />}
      </Spacing>
    </button>
  );
};
